	module.exports = {"data":{"wordpressPage":{"title":"products","content":"<p>In marketing, a product is anything that can be offered to a market that might satisfy a want or need.[1] In retailing, products are called merchandise. In manufacturing, products are bought as raw materials and sold as finished goods. A service is another common product type.</p>\n<p>Commodities are usually raw materials such as metals and agricultural products, but a commodity can also be anything widely available in the open market. In project management, products are the formal definition of the project deliverables that make up or contribute to delivering the objectives of the project. In insurance, the policies are considered products offered for sale by the insurance company that created the contract. In economics and commerce, products belong to a broader category of goods. The economic meaning of product was first used by political economist Adam Smith</p>\n","date":"April 30, 2018"},"site":{"id":"Site","siteMetadata":{"title":"HGS","subtitle":"HGS Site"}}},"pathContext":{"id":"0b80e4e9-712a-5853-b8bf-0c8b65c8eab4"}}

